// nodejs library that concatenates strings
import classnames from "classnames";
import React from "react";
import { Link } from "react-scroll";
// reactstrap components
import {
  Collapse,
  Container,
  Nav,
  Navbar,
  NavbarBrand,
  NavItem
} from "reactstrap";
import Logo from "../../assets/img/logo.png";

function ExamplesNavbar() {
  const [navbarColor, setNavbarColor] = React.useState("navbar-fade-black");
  const [navbarCollapse, setNavbarCollapse] = React.useState(false);

  const toggleNavbarCollapse = () => {
    setNavbarCollapse(!navbarCollapse);
    document.documentElement.classList.toggle("nav-open");
  };

  React.useEffect(() => {
    const updateNavbarColor = () => {
      if (
        document.documentElement.scrollTop > 299 ||
        document.body.scrollTop > 299
      ) {
        setNavbarColor("");
      } else if (
        document.documentElement.scrollTop < 300 ||
        document.body.scrollTop < 300
      ) {
        setNavbarColor("navbar-fade-black");
      }
    };

    window.addEventListener("scroll", updateNavbarColor);

    return function cleanup() {
      window.removeEventListener("scroll", updateNavbarColor);
    };
  });
  return (
    <Navbar
      className={classnames("fixed-top", navbarColor)}
      color-on-scroll="300"
      expand="lg"
    >
      <Container>
        <div className="navbar-translate">
          <NavbarBrand href="/" data-placement="bottom">
            <img alt="Eupep Logo" className="brand-logo" src={Logo}></img>
          </NavbarBrand>
          <button
            aria-expanded={navbarCollapse}
            className={classnames("navbar-toggler navbar-toggler", {
              toggled: navbarCollapse
            })}
            onClick={toggleNavbarCollapse}
          >
            <span className="navbar-toggler-bar bar1" />
            <span className="navbar-toggler-bar bar2" />
            <span className="navbar-toggler-bar bar3" />
          </button>
        </div>
        <Collapse className="justify-content" navbar isOpen={navbarCollapse}>
          <Nav navbar>
            <NavItem>
              <Link
                activeClass="active"
                to="home"
                spy={true}
                smooth={true}
                offset={-50}
                duration={500}
              >
                Home
              </Link>
            </NavItem>
            <NavItem>
              <Link
                activeClass="active"
                to="about"
                spy={true}
                smooth={true}
                offset={-50}
                duration={500}
              >
                About
              </Link>
            </NavItem>
            <NavItem>
              <Link
                activeClass="active"
                to="ventures"
                spy={true}
                smooth={true}
                offset={-50}
                duration={500}
              >
                Ventures
              </Link>
            </NavItem>
            <NavItem>
              <Link
                activeClass="active"
                to="services"
                spy={true}
                smooth={true}
                offset={-50}
                duration={500}
              >
                Services
              </Link>
            </NavItem>
            <NavItem>
              <Link
                activeClass="active"
                to="works"
                spy={true}
                smooth={true}
                offset={-50}
                duration={500}
              >
                Works
              </Link>
            </NavItem>
            <NavItem>
              <Link
                activeClass="active"
                to="contact"
                spy={true}
                smooth={true}
                offset={-50}
                duration={500}
              >
                Contact
              </Link>
            </NavItem>
          </Nav>
        </Collapse>
      </Container>
    </Navbar>
  );
}

export default ExamplesNavbar;
